import common from '../../common/common.js'
import util from '../../common/util.js'
import uniPopup from '../../components/uni-popup.vue'

export default {
	data() {
		return {
			userid:'',
			userInfo:[],
			question:[],
			hotline:'',		//客服热线
			questionList:[],
			info:{
				title:'合作加盟',
				delta:1
			},
			titleHeight:0,
			statusBarHeight:0,
			naviBarHeight:0,
		}
	},
	onLoad() {
		var that = this
		var userid = uni.getStorageSync('userid');
		var userInfo = uni.getStorageSync('userInfo');
		
		that.userid = userid
		that.userInfo = userInfo
		
		var currentPages = getCurrentPages()
		that.info.delta = currentPages.length
		
		that.titleHeight = common.titleHeight;
		that.statusBarHeight = common.statusBarHeight;
		that.naviBarHeight = common.naviBarHeight;
		
		that.getQuestionList();
		that.getHotline();
		
	
	},
	onPullDownRefresh:function(){
	  var that = this
	  uni.showNavigationBarLoading();
	  console.log('下拉刷新中...');	
	  that.getQuestionList();
	  that.getHotline();
	  
	  uni.hideNavigationBarLoading();
	  uni.stopPullDownRefresh();
	},
	methods: {
		//详情
		//获取常见问题
		getQuestionList(){
			var that = this
			uni.request({
				url: common.apiUrl + 'user/getQuestionList',
				method: 'GET',
				data: {},
				success: res => {
					console.log(res)
					that.questionList = res.data.question_list
				},
				fail: () => {},
				complete: () => {}
			});
		},
		
		//拨打客服电话
		phoneContact(){
			var that = this
			uni.makePhoneCall({
				phoneNumber:that.hotline
			})
		},
		
		//获取客服电话
		getHotline(){
			var that = this
			uni.request({
				url: common.apiUrl + 'user/getHotline',
				method: 'GET',
				data: {
				},
				success: res => {
					that.hotline = res.data.hotline
				},
				fail: () => {},
				complete: () => {}
			});
		},
		

		//链接
		jumpUrl(e){
			var that = this
			var url = e.currentTarget.dataset.url;
			uni.navigateTo({
				url:url
			})
		},
	}
}
